<template>
  <div class="container">
    <span v-if="usuarioDB.data">Bienvenido: {{usuarioDB.data.nombre}}</span>
    <h1>Administracion de Productos</h1>
    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged">
      {{ mensaje.texto }}
    </b-alert>

    <form @submit.prevent="editarProducto(productoEditar)" enctype="multipart/form-data" v-if="editar">
      <h3>Editar producto</h3>
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model.trim="productoEditar.nombre">
      <input type="text" class="form-control my-2" placeholder="Descripcin" v-model.trim="productoEditar.descripcion">
      <input type="number" class="form-control my-2" placeholder="Existencia" v-model="productoEditar.existencia">
      <input type="number" class="form-control my-2" placeholder="Costo Unitario en MXN" v-model="productoEditar.costoUnitario">
      <input type="number" class="form-control my-2" placeholder="Precio Unitario en MXN" v-model="productoEditar.precioUnitario">
      
      <input type="file" name="image" class="form-control my-2" ref="fileInput" @change="obtenerImagen($event)">

      <label>Categoria</label>
      <select class="form-control my-2" v-model="productoEditar.categoria">
          <option value="Alimentos">Alimentos</option>
          <option value="Bebidas">Bebidas</option>
          <option value="Reposteria">Reposteria</option>
          <option value="Otros">Otros</option>
      </select>

      <button type="submit" class="btn btn-success my-1 mx-2">Editar</button>
      <button type="submit" class="btn btn-secondary my-1" @click="editar = false">cancelar</button>
    </form>

    <form @submit.prevent="agregarProducto()" enctype="multipart/form-data" v-if="!editar">
      <h3>Agregar nuevo producto</h3>
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model.trim="producto.nombre">
      <input type="text" class="form-control my-2" placeholder="Descripcin" v-model.trim="producto.descripcion">
      <label>Existencia</label>
      <input type="number" class="form-control my-2" min="0" v-model="producto.existencia">
      <label>Costo Unitario MXN</label>
      <input type="number" class="form-control my-2" min="0" v-model="producto.costoUnitario">
      <label>Precio Unitario MXN</label>
      <input type="number" class="form-control my-2" min="0" v-model="producto.precioUnitario">
      
      <input type="file" name="image" class="form-control my-2" ref="fileInput" @change="obtenerImagen($event)">
      
      <label>Categoria</label>
      <select class="form-control my-2" v-model="producto.categoria">
          <option value="Alimentos">Alimentos</option>
          <option value="Bebidas">Bebidas</option>
          <option value="Reposteria">Resposteria</option>
          <option value="Otros">Otros</option>
      </select>
      
      <div class="d-grid gap-2">
        <button type="submit" class="btn btn-success my-2">Agregar</button>
      </div>

    </form>

    <table class="table">
      <thead>
        <tr>
          <th scope="col">Nombre</th>
          <th scope="col">Descripcin</th>
          <th scope="col">Existencia</th>
          <th scope="col">Costo U.</th>
          <th scope="col">Precio U.</th>
          <th scope="col">Categoria</th>
          <th scope="col">utilidad</th>

          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in productos" :key="index">
          <td id="producto">
            {{ item.nombre }}
            <img :src="'http://localhost:3000/uploads/'+item.image">
          </td>
          <td>{{ item.descripcion }}</td>
          <td>{{ item.existencia }}</td>
          <td>{{ item.costoUnitario }}</td>
          <td>{{ item.precioUnitario }}</td>
          <td>{{ item.categoria }}</td>
          <td>{{ item.precioUnitario - item.costoUnitario }}</td>
          
          <td>
            <b-button class="btn-danger btn-sm" @click="eliminarProducto(item._id)">Eliminar</b-button>
            <b-button class="btn-primary btn-sm m-1" @click="activarEdicion(item._id)">Editar</b-button>
          </td>

        </tr>
      </tbody>
    </table>

    <nav aria-label="Page navigation example">
      <ul class="pagination">
        <li class="page-item" :class="{ disabled: paginaActual === 1 }">
          <router-link class="page-link" :to="{ query: { pagina: paginaActual - 1 } }">
            Anterior
          </router-link>
        </li>
        <li class="page-item" :class="{active: paginaActual === index + 1 }" v-for="(item, index) in cantidadPaginas" :key="index">
          <router-link :to="{ query: { pagina: index + 1 } }" class="page-link" >{{ index + 1 }}</router-link>
        </li>
        <li class="page-item" :class="{ disabled: paginaActual === cantidadPaginas }">
          <router-link class="page-link" :to="{ query: { pagina: paginaActual + 1 } }">
            Siguiente
          </router-link>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      //los siguientes tres atributos son para la paginacion
      totalProductos: 0,
      limite: 20,
      paginaActual: 1,

      productos: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      mensaje: { color: "", texto: "" },
      producto: { nombre: "", descripcion: "", existencia: 0, costoUnitario: 0, precioUnitario: 0, image: "", categoria: "" },
      file: null,
      
      productoEditar: {},
      editar: false,
    };
  },
  computed: {
    ...mapState(["token", "usuarioDB"]),
    //este metodo se hace en las propiedades computadas para que siempre calcule en numero de paginas de manera dinamica
    cantidadPaginas(){
      //la funcion matematica redondea hacia arriba para que no existan paginas decimales
      return Math.ceil(this.totalProductos / this.limite)
    }
  },
  methods: {
    obtenerImagen(event){
      this.file = event.target.files[0];
    },
    
    paginacion(pagina) {
      let config = {
        headers: {token: this.token,},
      };
      //esta formula calcula el salto de datos para hacer la paginacion
      let skip = (pagina - 1) * this.limite;
      //esta ruta la lee el back end, no aparece en el navegador ni en el cliente de alguna forma
      this.axios
        .get(`/producto/ver-paginados?limite=${this.limite}&skip=${skip}`, config)
        .then((res) => {
          //console.log(res.data);
          this.productos = res.data.productoDB;
          this.totalProductos = res.data.totalProductos;
        })
        .catch((err) => {
          console.log(err.response);
        });
    },
    async listarProductos() {
      //envio del token como segundo parametro en la ruta
      let config = {
        headers: {
          token: this.token,
        },
      };
      //con vue axios de puede hacer los llamados de manera directa
      //la base_url tambien esta configurada en el main.js
      this.axios.get("/producto/ver-paginados", config).then((res) => {
        //console.log(res.data);
        this.productos = res.data.productoDB;
      });
      
    },
    agregarProducto() {
      //procesamiento del formulario para mandar imagenes
      let formData = new FormData();
      formData.append('nombre', this.producto.nombre);
      formData.append('descripcion', this.producto.descripcion);
      formData.append('existencia', this.producto.existencia);
      formData.append('costoUnitario', this.producto.costoUnitario);
      formData.append('precioUnitario', this.producto.precioUnitario);
      formData.append('image', this.file);
      formData.append('categoria', this.producto.categoria);
      //envio del token como tercer parametro en la ruta y el segundo es el body
      
      let config = {
        headers: {token: this.token}
      };
      this.axios
        .post("/producto/nuevo", formData, config)
        .then((res) => {
          this.productos.push(res.data);
          this.producto.nombre = "";
          this.producto.descripcion = "";
          this.producto.existencia = 0;
          this.producto.costoUnitario = 0;
          this.producto.precioUnitario = 0;

          const input = this.$refs.fileInput;
          input.type = 'text';
          input.type = 'file';
          this.file = null;

          this.producto.categoria = ""

          this.mensaje.color = "success";
          this.mensaje.texto = "Producto agregado";
          this.showAlert();
        })
        .catch((err) => {
          console.log(err.response);
          if (err.response.data.error.errors.nombre.message) {
            this.mensaje.texto = err.response.data.error.errors.nombre.message;
          } else {
            this.mensaje.texto = "Error del sistema";
          }
          this.mensaje.color = "danger";

          this.showAlert();
        });
    },
    eliminarProducto(id) {
        let config = {
        headers: {token: this.token}
        };

        this.axios
        .delete(`/producto/eliminar/${id}`, config)
        .then((res) => {
          const index = this.productos.findIndex(
            (item) => item._id === res.data._id
          );
          //splice borra la candidad de elementos indicados que coincidan con el este index
          this.productos.splice(index, 1);
          this.mensaje.color = "success";
          this.mensaje.texto = "Nota eliminada";
          this.showAlert();
        })
        .catch((err) => {
            console.log(err.response)
        });
    },
    activarEdicion(id) {
      //resetea el input del formulario para no causar interferencia con las imagenes
      const input = this.$refs.fileInput;
      input.type = 'text';
      input.type = 'file';
      this.file = null;

      let config = {
        headers: {token: this.token}
      };
      this.editar = true;
      this.axios.get(`/producto/ver-unico/${id}`, config).then((res) => {
        console.log(res.data);
        this.productoEditar = res.data;
      });
    },

    editarProducto(item) {
      let formData = new FormData();
      formData.append('nombre', item.nombre);
      formData.append('descripcion', item.descripcion);
      formData.append('existencia', item.existencia);
      formData.append('costoUnitario', item.costoUnitario);
      formData.append('precioUnitario', item.precioUnitario);
      formData.append('categoria', item.categoria);

      if(this.file != null){
        formData.append('image', this.file);
      }
      
      let config = {
        headers: {token: this.token}
      };

      this.axios.put(`/producto/actualizar/${item._id}`, formData, config).then((res) => {
        const index = this.productos.findIndex((n) => n._id === res.data._id);
        this.productos[index].nombre = res.data.nombre;
        this.productos[index].descripcion = res.data.descripcion;
        this.productos[index].existencia = res.data.existencia;
        this.productos[index].costoUnitario = res.data.costoUnitario;
        this.productos[index].precioUnitario = res.data.precioUnitario;
        this.productos[index].image = res.data.image;
        this.productos[index].categoria = res.data.categoria;

        const input = this.$refs.fileInput;
        input.type = 'text';
        input.type = 'file';
        this.file = null;
        
        this.mensaje.color = "success";
        this.mensaje.texto = "Producto editado";
        this.showAlert();
        this.editar = false;
      });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
  },
  
  watch: {
    //esta lee una ruta url con el parametro 'pagina' y captura el valor el el handler
    "$route.query.pagina": {
      immediate: true,
      //handler es una palabra reservada, ejecuta el metodo dle observador
      handler(pagina) {
        pagina = parseInt(pagina) || 1;
        this.paginacion(pagina);
        this.paginaActual = pagina;
      },
    },
  },
};
</script>

<style scoped>
#producto{
  display: flex;
  flex-direction: column;
}
#producto img{
  width: 50px;
  height: 50px;
}

</style>